<template>
  <div class="userInfo">
    <!--  -->
    <!--  <div class="bre-crumb">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>  
        </el-breadcrumb>
     </div> -->
    <!--  -->
    <div class="info-top">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="">
          <el-select v-model="formInline.region" placeholder="">
            <el-option label="" value="leader"></el-option>
            <el-option label="" value="employee"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
        <el-form-item label="">
          <el-input v-model="formInline.user" placeholder="//"></el-input>
        </el-form-item>
        <el-button type="primary" v-waves icon="search" @click="handleSearch"></el-button>
        <el-button type="primary" v-waves icon="edit" @click="handleCreate"></el-button>
        <el-button type="primary" v-waves icon="edit" @click="handleDelAll"></el-button>
        <el-button type="primary" v-waves icon="document" @click="handleDownload"></el-button>

        </el-form-item>
      </el-form>
    </div>
    
    <!--  -->
    <div class="add">
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="ID" :label-width="formLabelWidth">
              <el-input v-model="form.id" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="form.type" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.phoneNumber" auto-complete="off"></el-input>
          </el-form-item>
           <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.address" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.date" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button v-waves @click="dialogFormVisible = false"> </el-button>
          <el-button v-waves type="primary" @click="handleCreateSubmit"> </el-button>
        </div>
      </el-dialog>
      <router-view></router-view>
    </div>

    <!--  -->
    <div class="info-con"> 
      <el-table ref="multipleTable" :data="tableData3" border tooltip-effect="dark" style="width: 100%"
       @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column v-model="form.id" prop="id" label="ID" width="60"></el-table-column>
          <el-table-column v-model="form.name" prop="name" label="" width="120"></el-table-column>
          <el-table-column v-model="form.type" prop="type" label="" width="120"></el-table-column>
          <el-table-column v-model="form.phoneNumber" prop="phoneNumber" label="" width="130"></el-table-column>
          <el-table-column v-model="form.address" prop="address" label="" width="240" show-overflow-tooltip></el-table-column>
          <el-table-column v-model="form.date" label="" width="120"> <template scope="scope">{{ scope.row.date }}</template></el-table-column>
          <el-table-column label="" width="150">
            <template scope="scope">
              <el-button v-waves size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button v-waves size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
      </el-table>
      <div style="margin-top: 20px">
        <el-button v-waves @click="toggleSelection([tableData3[0], tableData3[1],tableData3[2],tableData3[3],tableData3[4],tableData3[5],tableData3[6]])"></el-button>
        <el-button v-waves @click="toggleSelection()"></el-button>
      </div>
    </div>

    <!--  -->
    <div class="add">
      <el-dialog title="" :visible.sync="dialogFormVisible1">
        <el-form :model="editForm">
          <el-form-item label="ID" :label-width="formLabelWidth">
              <el-input v-model="editForm.id" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="editForm.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="editForm.type" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="editForm.phoneNumber" auto-complete="off"></el-input>
          </el-form-item>
           <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="editForm.address" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="editForm.date" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
         
            <el-button v-waves @click="dialogFormVisible1 = false"></el-button>
            <el-button v-waves type="primary" @click="handledoneEdit()"></el-button>
          
        </div>
      </el-dialog>
      <router-view></router-view>
    </div>

    <div class="info-footer">
        <div class="block">
           <span class="demonstration"></span>
           <el-pagination
             @size-change="handleSizeChange"
             @current-change="handleCurrentChange"
             :current-page="currentPage4"
             :page-sizes="[25, 50, 75, 100]"
             :page-size="25"
             layout="total, sizes, prev, pager, next, jumper"
             :total="100">
           </el-pagination>
         </div>

    </div>

  </div>
 
</template>

<script>
     import {api} from '@/global/api'
     export default {
       data() {
         return {
           formInline: {
             user: '',
             region: ''
           },
           editIndex: null,
           editRow: null,
           delIndex: null,
           tableData3:null,//JSon
           // 
           dialogFormVisible: false,
           dialogFormVisible1: false,
           form: {
                 id: '',
                 name: '',
                 type: '',
                 phoneNumber: '',
                 address: '',
                 date: ''
           },
           editForm: {
                 id: '',
                 name: '',
                 type: '',
                 phoneNumber: '',
                 address: '',
                 date: ''
           },
           formLabelWidth: '120px',
           //JSon
          
          multipleSelection: [],//
          // 
          currentPage4: 4

         }
       },
       mounted:function(){
        var vm=this;
        vm.getData();//JSON
       },
       methods: {
        //json
         getData(){
          // return this.bb;

          this.$http.get(api.userInfo).then(function(response){
              // alert(1);
            console.log('response',response)
            // console.log("json",response.data.tableData3)
            this.tableData3 = response.data.tableData3;

          }, function(response){
            alert("")
          })

         },
         //  
         handleSearch() {
           // console.log('submit!');
           var vm = this;
           // 
           this.$http.get(api.userInfo2,{params:vm.formInline}).then(function(response){
               // alert(1);
             console.log('response',response)
             // console.log("json",response.data.tableData3)
             this.tableData3 = response.data.tableData3;

           }, function(response){
             alert("")
           })



           // for(var i = 0; i < vm.tableData3.length+1; i++){//

           //     for(var item of vm.tableData3){//
           //         // console.log(item)
           //         if(vm.formInline.user != item.id){
           //            var index = vm.tableData3.indexOf(item);
           //            console.log(index)
           //            vm.tableData3.splice(index,1);//indexidindex

           //          }
           //     }
           // }

         },
         //form
         initForm(){
            let vm = this;
            vm.form = {
                    id: '',
                    name: '',
                    type: '',
                    phoneNumber: '',
                    address: '',
                    date: ''
            }
         },
         // 
         handleCreate(){
           //
           this.initForm();

            this.dialogFormVisible = true;
         },
         // 
         handleCreateSubmit(){
            var vm = this;
            console.log('',vm.form);
            vm.tableData3.push(vm.form);//
            console.log('',vm.tableData3);
            this.dialogFormVisible = false;
         },
         //
         handleEdit(index, row) {
             this.dialogFormVisible1 = true;
            // console.log("",index, "row",row);
           
            // this.editForm = row;//=
            this.editForm = JSON.parse(JSON.stringify(row)); //row
            // this.$forceUpdate()//
             this.editIndex = index;

          
         },
         //
         handledoneEdit(){
           // console.log("");
              
            console.log(this.editIndex);
            console.log(this.editForm)
            var vm = this;
             // // vm.tableData3.push(vm.editForm);
            vm.tableData3[this.editIndex] = this.editForm;
            vm.tableData3.splice(this.editIndex,1,this.editForm);//N
            // console.log(vm.tableData3[this.editIndex]);
            // store.dispatch(vm.tableData3[this.editIndex]);
            this.dialogFormVisible1 = false;

         },
         //
         handleDelete(index, row) {  
            console.log("index",index);
            console.log("row",row);
             // console.log(index, row);
            this.tableData3.splice(index,1);//

         },
         //
         handleDelAll(){
            let vm = this;
            console.log("row",vm.multipleSelection);//
            //splice vm.multipleSelection forid[1,3,5]
            for(var i = 0; i < vm.multipleSelection.length; i++){
              console.log(vm.multipleSelection[i]);//
              // console.log(vm.multipleSelection[i].id);
              var ids = this.multipleSelection.map(item => item.id).join()
              //id
              //  //
              // ids = Array.prototype.slice.call(ids);
              // console.log(ids instanceof Array);
             

            }
            console.log("id",ids);

            //dug
            // this.tableData3.splice(vm.multipleSelection,vm.multipleSelection.length);
            // console.log("",vm.multipleSelection.length);


            // 
            // let arr = [];
            // var len = vm.multipleSelection.length;
            // console.log(vm.multipleSelection.indexOf(i));//-1
            // for (var i = 0; i < len; i++) {
            //   if (vm.multipleSelection.indexOf(i)>=0) {
            //     console.log(vm.multipleSelection.indexOf(i))
            //   }else{
            //     arr.push(vm.multipleSelection[i])
            //   }
            // }
            // vm.multipleSelection = arr;
            // this.multipleSelection = [];                                                                                                 

         },
         //
         handleDownload(){
            console.log("");
         },

         //
         toggleSelection(rows) {
           if (rows) {
             rows.forEach(row => {
               this.$refs.multipleTable.toggleRowSelection(row);
             });
           } else {
             this.$refs.multipleTable.clearSelection();
           }
         },
         //
         handleSelectionChange(val) {
           this.multipleSelection = val; //[]
         },
         //
         handleSizeChange(val) {
          console.log(` ${val} `);
         },
        handleCurrentChange(val) {
          console.log(`: ${val}`);
         },



       }
     }
</script>

<style>
  * {
    padding: 0;
    margin: 0;
  }
  .userInfo{
    position: absolute;
  }
  .info-top{
    margin: 20px 10px;
  }
  .info-con{
    margin: 20px 10px;
  }
 
</style>


