<template>
  <div class="new-user">
    <!--  -->
   <!--  <div class="bre-crumb">
       <el-breadcrumb separator="/">
         <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
         <el-breadcrumb-item></el-breadcrumb-item>  
       </el-breadcrumb>
    </div> -->

    <!--  -->
    <div class="ruleForm">
      <h1 class="title"></h1>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="region">
          <el-select v-model="ruleForm.region" placeholder="">
            <el-option label="" value="leader"></el-option>
            <el-option label="" value="employee"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" required>
          <el-col :span="11">
            <el-form-item prop="date1">
              <el-date-picker type="date" placeholder="" v-model="ruleForm.date1" style="width: 100%;"></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-form-item prop="date2">
              <el-time-picker type="fixed-time" placeholder="" v-model="ruleForm.date2" style="width: 100%;"></el-time-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="delivery">
          <el-switch on-text="" off-text="" v-model="ruleForm.delivery"></el-switch>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-checkbox-group v-model="ruleForm.type">
            <el-checkbox label="" name="type"></el-checkbox>
            <el-checkbox label="" name="type"></el-checkbox>
            <el-checkbox label="" name="type"></el-checkbox>
            <el-checkbox label="" name="type"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="sex">
          <el-radio-group v-model="ruleForm.sex">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="birthday">
          <el-date-picker v-model="ruleForm.birthday" type="date" placeholder="" :picker-options="pickerOptions0"></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="desc">
          <el-input type="textarea" v-model="ruleForm.desc"></el-input>
        </el-form-item>
        <el-form-item class="btn">
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </div>

  </div>
</template>
<script>
  export default {
      data () {
          return {
            ruleForm: {
            name: '',
            region: '',
            date1: '',
            date2: '',
            delivery: false,
            type: [],
            sex: '',
            birthday: '',
            desc: ''
        },
            rules: {
              name: [
                { required: true, message: '', trigger: 'blur' },
                { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
              ],
              region: [
                { required: true, message: '', trigger: 'change' }
              ],
              date1: [
                { type: 'date', required: true, message: '', trigger: 'change' }
              ],
              date2: [
                { type: 'date', required: true, message: '', trigger: 'change' }
              ],
              type: [
                { type: 'array', required: true, message: '', trigger: 'change' }
              ],
              sex: [
                { required: true, message: '', trigger: 'change' }
              ],
              birthday: [
                { type: 'date', required: true, message: '', trigger: 'change' }
              ],
              desc: [
                { required: true, message: '', trigger: 'blur' }
              ]
            },
            pickerOptions0: {
                     disabledDate(time) {
                       return time.getTime() < Date.now() - 8.64e7;
                     }
                   }

          };
        },
        methods:{
            submitForm(formName) {
                 this.$refs[formName].validate((valid) => {
                   if (valid) {
                     alert('submit! ');
                     console.log("",JSON.stringify(this.ruleForm));
                   } else {
                     console.log('error submit!!');
                     return false;
                   }
                 });
               },
               resetForm(formName) {
                 this.$refs[formName].resetFields();
               }
         }

        }

      

</script>

<style>
  * {
    padding: 0;
    margin: 0;
  }
  .new-user{
    position: absolute;
  }
  .new-user .title{
    text-align: center;
    margin-bottom: 50px;
  }
  .new-user .el-col-2{
    text-align: center;
  }
  .new-user .btn{
    margin-left: 100px;
  }

  
</style>


